.news-flex{
    display:flex;
    flex-direction:column;
}

.news-flex > .news-card-flex{
    flex:auto;
    display:flex; 
    flex-direction:column;
    margin-left:0.75em;
    margin-right:0.75em;
    margin-bottom: 1em;
    max-height: 220px;
    border-radius:10px;
}
.news-card-flex > .news-row {
    flex:auto;
    display:flex; 
    flex-direction:row;
}

.news-card-flex  .news-content {
    flex-grow:3;
    padding : 0.5em;
    overflow: hidden;
}
.news-card-flex   .news-img-container {
    flex-grow:0;
    border-radius:10px 0 0 10px;
    
}
.news-img-container > .news-img {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.05);
    border-radius:10px 0 0 10px;
}

@media(max-width: 700px) {
    .news-img-container > .news-img {
        max-width: 150px; 
        border-radius:5px;
        margin: 15px;
    }

    .news-card-flex  .alternative-title {
        display: block !important;
        margin: 0.5em;
    }

    .news-card-flex  .main-title {
        display: none;
    }
}

.news-card-flex  .article-title {
    font-size: x-large;
    margin-bottom: 0.5em;
    margin-top: 0.25em;
    font-weight: bold;
}
.news-card-flex  .alternative-title {
    display: none;
}


.news-card-flex .article-header {
    font-weight: lighter;
    color: #909090;
}

.news-card-flex .source-header {
    font-weight: lighter;
    color: #505050 !important;
    text-decoration: underline;
}

.shadowed{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
